@import url("https://fonts.googleapis.com/css?family=Baloo+Bhai");
@import url("https://fonts.googleapis.com/css?family=Lobster");
@import url("https://fonts.googleapis.com/css?family=Parisienne");

h1 {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

input:hover,
input:active,
input:hover,
input:focus {
  outline: none;
}

body {
  margin: 0;
  padding: 0;
  background-color: rgba(32, 178, 171, 0.719);
}

.container-clock {
  padding-left: calc(50% - 450px);
  height: 100vh;
  width: auto;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
}

.etiqueta {
  position: fixed;
  transform: rotate(44deg);
  top: 125px;
  right: -116px;
  height: 60px;
  width: 554px;
  display: flex;
  justify-content: center;
  align-items: center;
  h3 {
    font-family: "Baloo Bhai", cursive;
  }
}

#augus {
  color: #cbd6df;
  text-decoration: none;
  font-size: 30px;
  font-family: "Parisienne", cursive;
}

.logo {
  width: 40px;
  height: 40px;
  vertical-align: middle;
  margin: 5px;
}
.redes-c {
  position: fixed;
  top: 10px;
  left: 10px;
}
.redes {
  padding: 5px;
  margin: 4px;
  border-radius: 10px;
  background-color: #ffffffab;
  width: 80px;
  height: 80px;
  object-fit: cover;
  transform: scale(1);
  transition: transform 1s;
}
.redes:hover {
  transform: scale(1.3);
}
