@import "colores";

@function makelongshadow($color, $tama単o) {
  $val: 0px 0px $color;
  @for $i from 1 through $tama単o {
    $val: #{$val}, #{$i}px #{$i}px #{$color};
  }
  @return $val;
}
@mixin longshadow($color, $tama単o) {
  box-shadow: makelongshadow($color, $tama単o);
}


@font-face {
  font-family: "Digital-7";
  src: url("../../fonts/Digital-7.ttf.woff") format("woff"),
    url("../../fonts/Digital-7.ttf.svg#Digital-7") format("svg"),
    url("../../fonts/Digital-7.ttf.eot"),
    url("../../fonts/Digital-7.ttf.eot?#iefix") format("embedded-opentype");
  font-weight: normal;
  font-style: normal;
}

.Augus {
  color: black;
  font-family: 'Lobster', cursive; 
  font-size: 58px;
  margin-left: 20px;
  padding-bottom: 20px;
  text-shadow: 3px 2px #fffc;
}

.container-clock-b {
  max-width: 800px;
  height: 300px;
  border-radius: 20px;
  border: 6px solid #031522;
  background-color: rgb(11, 65, 82);
  @include longshadow(#2c81a3, 50);
}

.header-clock {
    background-color:rgb(45, 107, 116);
      height: 80px;
  border-radius: 10px 10px 0 0;
  display: flex;
  align-items: center;
}
.pup {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Baloo Bhai", cursive;
  font-weight: bold;
  margin-left: 10px;
  width: 30px;
  height: 30px;
  border-radius: 10px;
  box-shadow: 5px 2px 1px #0f3049;
  cursor: pointer;
}
.pup:active {
  box-shadow: none;
  transform: translate(5px, 2px);
}

.screen {
  height: 60px;
  border-radius: 10px;
  width: 450px;
  margin-left: 10px;
  background-color: black;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.hora {
  display: flex;
  justify-content: center;
  align-items: center;
  h1 {
    font-family: "Digital-7";
    color: white;
    font-size: 60px;
  }
  width: 300px;
}

.options {
  width: 150px;
  color: white;
  font-family: "Digital-7";
  ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    list-style: none;
    margin: 0;
    padding: 0;
    li {
      font-size: 25px;
    }
  }
}

.init {
  opacity: 0;
}

.on {
    animation: encendido 2s;
    animation-fill-mode: forwards
}

@keyframes encendido{
    from{
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.off {
    animation: apagado 2s;
    animation-fill-mode: forwards
}

@keyframes apagado{
    from{
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}