.init {
  opacity: 0; }

.on {
  -webkit-animation: encendido 2s;
          animation: encendido 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

@-webkit-keyframes encendido {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes encendido {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.off {
  -webkit-animation: apagado 2s;
          animation: apagado 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

@-webkit-keyframes apagado {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes apagado {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

