.container-perf {
  width: 100%;
  height: 100%;
  text-align: center;
  display: flex; }

.options-perf {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 212px;
  padding-top: 10px; }
  .options-perf img {
    clip-path: circle(75px at 74px 41px);
    width: 150px; }

.button-perf {
  width: 100px;
  height: 40px;
  margin-bottom: 8px;
  border-radius: 10px;
  border: none;
  box-shadow: 1px 1px black, 2px 2px black, 3px 3px black, 4px 4px black, 5px 5px black;
  font-size: 18px;
  font-family: "Baloo Bhai", cursive;
  color: rgba(255, 255, 255, 0.87);
  cursor: pointer; }

.button-perf:active {
  transform: translate(5px, 5px);
  box-shadow: none; }

.screen-perf {
  width: 700px;
  display: flex;
  justify-content: center; }

.screen-origin {
  background-color: black;
  color: white;
  height: 200px;
  width: 620px;
  border-radius: 10px;
  margin-top: 10px;
  margin-left: -18px;
  display: flex;
  align-items: center;
  justify-content: center; }
  .screen-origin h1 {
    font-family: "Digital-7";
    color: red;
    font-size: 185px;
    padding: 0;
    margin: 0; }
